import React from 'react';
// import { Text } from 'evolux-ui-v2';

type LayoutProps = React.HTMLAttributes<HTMLDivElement> & {
  title: string;
  children: React.ReactNode;
};

const Layout = ({ title, children }: LayoutProps) => {
  return (
    <div>
      <main>
        <p>{title}</p>
        {children}
      </main>
    </div>
  );
};

export default Layout;
